import React, { Component, Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import { Card, Row, Table, Popconfirm, Col, Select } from 'antd';
import Page from 'components/LayoutComponents/Page';
import { BtnEdit, BtnDelete, BtnAddNew, BtnSave } from 'components/common/button';
import { CartHeadAction } from 'components/common';
import { configTimeRegisOnPortal } from 'dump/data';
import './style.scss';

const { Option } = Select;

class ComeFrom extends Component {
  constructor(props) {
    super(props);
    this.handleDelete = this.handleDelete.bind(this);
    this.state = {
      loading: false,
    };
    this.title = 'Danh mục quy định thời gian trên cổng thông tin';
    this.urlEdit = '#/configTimeRegisOnPortal/form/edit/';
    this.urlOfBtnAdd = '#/configTimeRegisOnPortal/form/new/';
    this.columns = [
      {
        title: 'Hệ, khóa, khoa',
        dataIndex: 'label',
        key: 'label',
        // sorter: (a, b) => a.label.length - b.label.length,
        defaultSortOrder: 'descend',
      },
      {
        title: 'Đợt 1 từ ngày',
        dataIndex: 'fromTime',
        key: 'fromTime',
        // sorter: (a, b) => a.labelInEngLish.length - b.labelInEngLish.length,
        defaultSortOrder: 'descend',
      },
      {
        title: 'Đợt 1 đến ngày',
        dataIndex: 'toTime',
        key: 'toTime',
        // sorter: (a, b) => a.labelInEngLish.length - b.labelInEngLish.length,
        defaultSortOrder: 'descend',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) =>
          configTimeRegisOnPortal.length >= 1 ? (
            <Fragment>
              <BtnEdit href={`${this.urlEdit}${record.id}`} />
              <Popconfirm
                title="Bạn có muốn xóa?"
                okText="Xóa"
                cancelText="Hủy"
                onConfirm={() => this.handleDelete(record.id)}
              >
                <BtnDelete />
              </Popconfirm>
            </Fragment>
          ) : null,
      },
    ];
    // rowSelection object indicates the need for row selection
    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
    this.cssBetweenRow = { marginBottom: '8px' };
  }

  componentDidMount = async () => {
    // this.getComeFrom();
  };

  onChange = (value, selectedOptions) => {
    console.log(value, selectedOptions);
  };

  getComeFrom = async () => {
    // const res = await getComeFrom();
    this.setState({ loading: false });
  };

  handleDelete = () => {
    // deleteComeFrom(id).then( res => {
    //   if (res.status === 200) {
    //     message.success('Xóa xuất thân thành công.');
    //     this.getComeFrom();
    //   } else {
    //     message.error(res.message);
    //   }
    // });
  };

  render() {
    return (
      <Card title={this.title}>
        <CartHeadAction>
          <BtnAddNew href="#/configTimeRegisOnPortal/form/new" />
          <BtnSave title="Cho phép đăng ký" />
        </CartHeadAction>
        <Row style={{ marginBottom: '5px' }}>
          <Col span={3}>
            <Row>
              <Col span={24} className="label">
                Hệ:
              </Col>
            </Row>
            <Row style={{ display: 'flex', alignItems: 'center' }}>
              <Col span={24} style={{ display: 'flex', justifyContent: 'left' }}>
                <Select defaultValue="dh">
                  <Option value="dh">Đại học</Option>
                  <Option value="cd">Cao đẳng</Option>
                  <Option value="tc">Trung cấp</Option>
                </Select>
              </Col>
            </Row>
          </Col>
          <Col span={3}>
            <Row>
              <Col span={24} className="label">
                Kỳ:
              </Col>
            </Row>
            <Row style={{ display: 'flex', alignItems: 'center' }}>
              <Col span={24} style={{ display: 'flex', justifyContent: 'left' }}>
                <Select defaultValue="01">
                  <Option value="01">01</Option>
                  <Option value="02">02</Option>
                  <Option value="03">03</Option>
                </Select>
              </Col>
            </Row>
          </Col>
          <Col span={5}>
            <Row>
              <Col span={24} className="label">
                Năm:
              </Col>
            </Row>
            <Row style={{ display: 'flex', alignItems: 'center' }}>
              <Col span={24} style={{ display: 'flex', justifyContent: 'left' }}>
                <Select defaultValue="2018-2019">
                  <Option value="2014-2015">2014-2015</Option>
                  <Option value="2015-2016">2015-2016</Option>
                  <Option value="2016-2017">2016-2017</Option>
                  <Option value="2017-2018">2017-2018</Option>
                  <Option value="2018-2019">2018-2019</Option>
                </Select>
              </Col>
            </Row>
          </Col>
          <Col span={8}>
            <Row>
              <Col span={24} className="label">
                Kỳ đăng ký:
              </Col>
            </Row>
            <Row style={{ display: 'flex', alignItems: 'center' }}>
              <Col span={24} style={{ display: 'flex', justifyContent: 'left' }}>
                <Select defaultValue="01">
                  <Option value="01">Đợt 1 (Học kỳ 1 năm học 2018)</Option>
                  <Option value="02">Đợt 2 (Học kỳ 1 năm học 2018)</Option>
                  <Option value="03">Đợt 1 (Học kỳ 2 năm học 2018)</Option>
                  <Option value="03">Đợt 2 (Học kỳ 2 năm học 2018)</Option>
                </Select>
              </Col>
            </Row>
          </Col>
          <Col span={5}>
            <Row>
              <Col span={24} className="label">
                Khóa:
              </Col>
            </Row>
            <Row style={{ display: 'flex', alignItems: 'center' }}>
              <Col span={24} style={{ display: 'flex', justifyContent: 'left' }}>
                <Select defaultValue="2018-2019">
                  <Option value="2014-2015">2014-2015</Option>
                  <Option value="2015-2016">2015-2016</Option>
                  <Option value="2016-2017">2016-2017</Option>
                  <Option value="2017-2018">2017-2018</Option>
                  <Option value="2018-2019">2018-2019</Option>
                </Select>
              </Col>
            </Row>
          </Col>
        </Row>

        <Row>
          <Col span={24}>
            <Table
              rowKey="id"
              loading={this.state.loading}
              rowSelection={this.rowSelection}
              bordered
              dataSource={configTimeRegisOnPortal}
              columns={this.columns}
              onRow={() => ({
                onDoubleClick: () => {},
              })}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}
export default withRouter(props => (
  <Page {...props}>
    <ComeFrom {...props} />
  </Page>
));



